import { defHttp } from '/@/utils/http/axios';

enum Api {
  //设备统计
  getDeviceStatUrl = '/da/station-operation/monitor/deviceStat',

  //顶部指标
  getTopInfoUrl = '/da/station-operation/monitor/topInfo',

  //逆变器发电排行
  getInverterRankUrl = '/monitor/large-screen/inverter/powerRank',

  //告警总览
  getStationAlarmUrl = '/da/station-operation/monitor/alarm/overview',

  //实时告警
  getRealTimeAlarmUrl = '/da/station-operation/monitor/realTime/alarm',

  //离散数据分析
  getDispersionRatioUrl = '/monitor/large-screen/station/dispersionRatio',
}

export const getDeviceStatData = (params) =>
  defHttp.post({ url: Api.getDeviceStatUrl, params }, {});

export const getTopInfoData = (params) => defHttp.get({ url: Api.getTopInfoUrl, params }, {});

export const getInverterRankData = (params) =>
  defHttp.get({ url: Api.getInverterRankUrl, params }, {});

export const getStationAlarmData = (params) =>
  defHttp.get({ url: Api.getStationAlarmUrl, params }, {});

export const getRealTimeAlarmData = (params) =>
  defHttp.get({ url: Api.getRealTimeAlarmUrl, params }, {});

export const getDispersionRatioData = (params) =>
  defHttp.get({ url: Api.getDispersionRatioUrl, params }, {});

export const realTimeColumns = [
  {
    title: '序号',
    dataIndex: 'key',
    key: 'key',
    width: 50,
    align: 'center',
    customRender: ({ text, record, index }) => `${index + 1}`,
  },
  { title: '设备名称', dataIndex: 'device_name', key: 'deviceName', align: 'center' },
  { title: '告警名称', dataIndex: 'alarm_name', key: 'alarmName', align: 'center' },
  { title: '告警时间', dataIndex: 'first_alarm_time', key: 'firstAlarmTime', align: 'center' },
  {
    title: '告警时长',
    dataIndex: 'show_alarm_duration',
    key: 'showAlarmDuration',
    align: 'center',
  },
];
